@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button{padding:.4em .6em;background-color:#1d3c40;border-radius:.5em;border:solid 1px #1d3c40;outline:none;cursor:pointer;transition:.3s}.button:hover{background-color:transparent;color:#000}
.item-container{display:flex;flex-direction:column;justify-content:space-between;flex:0 1 30%;margin-bottom:1em;padding:1em;border-bottom:solid 1px #1d3c40;border-right:solid 1px #1d3c40;transition:.5s}.item-container:hover{box-shadow:0px 0px 50px 0px rgba(0,0,0,.29)}.item-containerImage{width:100%;height:40%;object-fit:cover}.item-containerTitle{font-size:1.5em}.item-containerBottomWrapper{display:flex;align-items:center;justify-content:space-between}
.error-wrapper{text-align:center;font-size:3em;margin-top:2em;color:#1d3c40}
.circle{height:3em;width:3em;border-radius:50%;background-color:#1d3c40;animation:flip 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;margin:.7em auto}@keyframes flip{0%,100%{animation-timing-function:cubic-bezier(0.5, 0, 1, 0.5)}0%{transform:rotateY(0deg)}50%{transform:rotateY(1800deg);animation-timing-function:cubic-bezier(0, 0.5, 0.5, 1)}100%{transform:rotateY(3600deg)}}
.items-container{display:flex;flex-wrap:wrap;justify-content:space-evenly;background-color:#bfb3a8}
.header-container{display:grid;grid-template-columns:repeat(2, 1fr);place-items:center;height:35vh;background-color:#1d3c40;color:#fff}.header-container .title{font-size:8em;text-transform:uppercase}.header-container .infos-wrapper{text-align:center;width:100%;border-left:solid 1px #fff}.header-container .infos-wrapper .info{font-size:3em;animation:appear 1.2s ease-in-out}@keyframes appear{0%{transform:scale(0)}70%{transform:scale(1.3)}100%{transform:scale(1)}}
.footer{height:2.5em;line-height:2.5em;text-align:center;background-color:#1d3c40;color:#fff}
*{padding:0;margin:0;box-sizing:border-box;font-family:"Montserrat",sans-serif}body{background-color:#bfb3a8}a{text-decoration:none;color:#fff}.main-container{min-height:calc(100vh - 2.5em);overflow:hidden}
